<template>
  <div class="queryModal">
    <!--<Button type="primary" @click="showModal"> New Tag</Button>-->
    <a-modal title="查询" :visible="visible" :maskClosable="maskClosable" @ok="handleOk" :confirmLoading="confirmLoading" @cancel="handleCancel" width="907px">
      <query :structure="structure" ref="dialogtreeQuery"></query>
    </a-modal>
  </div>
</template>

<script>
  import AModal from 'ant-design-vue/lib/modal';
  import query from "@/components/Query/Query"
  
  // import { constants } from 'http2';
  /*
   * EditableCell Code https://github.com/vueComponent/ant-design-vue/blob/master/components/table/demo/EditableCell.vue
   */
  export default {
    components: {
      AModal,
      query
    },
  data() {
    return {
      maskClosable:false,//点击蒙层不关闭modal
      ModalText: "Content of the modal",
      visible: false,
      confirmLoading:false,
     //查询条件的结构
      structure: {
        fields: [
          {
            id: 1,
            field: "userid",
            text:"用户",
            type: "input",
            options:[],
            isDuration:false,
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 2,
            field: "callerid",
            text:"发送号码",
            type: "input",
            options:[],
            isDuration:false,
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 3,
            field: "calleeid",
            text:"接受号码",
            type: "input",
            options:[],
            isDuration:false,
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 4,
            field: "dir",
            text:"方向",
            type: "select",
            options:["发送","接受"],
            isDuration:false,
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 5,
            field: "msgid",
            text:"消息ID",
            type: "input",
            options:[],
            isDuration:false,
            isNumber:true//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },
          {
            id: 6,
            field: "state",
            text:"状态",
            type: "select",
            options:["等待发送","成功","失败"],
            isDuration:false,
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },

          {
            id: 7,
            field: "send_time",
            text:"发送时间",
            type: "date",
            options:[],
            isDuration:false,
            isNumber:false//搜索的值是否为数值，如果为数值那么条件类型据此来变换
          },

        ],
        stringConditions: [
          {
            id: 1,
            condition: "=",
            text:"等于",
            type: "input"
          },
          {
            id: 2,
            condition: "like_contain",
            text:"包含",
            type: "input"
          }
        ],
        numberConditions: [
          {
            id: 1,
            condition: "=",
            text:"等于",
            type: "input"
          },
          {
            id: 2,
            condition: "!=",
            text:"不等于",
            type: "input"
          },
          {
            id: 3,
            condition: ">",
            text:"大于",
            type: "input"
          },
          {
            id: 4,
            condition: ">=",
            text:"大于等于",
            type: "input"
          },
          {
            id: 5,
            condition: "<",
            text:"小于",
            type: "input"
          },
          {
            id: 6,
            condition: "<=",
            text:"小于等于",
            type: "input"
          }
        ],
        connectionWay: [
          {
            connection: "与"
          },
          {
            connection: "或"
          }
        ]
      },
    };
  },
  mounted(){
  },
  methods: {
    showQueryModal() {
      this.visible = true;
    },
    handleOk(e) {
      let  _this = this;
      this.$refs.dialogtreeQuery.dataSource.forEach(function(item) {
        if (item.fieldSelected) {
          _this.$refs.dialogtreeQuery.searchConditionResult = [
            ..._this.$refs.dialogtreeQuery.searchConditionResult,
            {
              key: item.key || {},
              field: item.fieldSelected || {},
              condition: item.conditionSelected || {},
              value: item.value || {}
            }
          ];
        }
      });
      this.ModalText = "The modal will be closed after two seconds";
      this.confirmLoading = true;
      setTimeout(() => {
        this.visible = false;
        this.confirmLoading = false;
        alert(JSON.stringify(this.$refs.dialogtreeQuery.searchConditionResult));
        this.$refs.dialogtreeQuery.searchConditionResult = [];
      }, 2000);
      //重置
    },
    handleCancel(e) {
      this.visible = false;
    },
  }
};
</script>



